@charset "utf-8";

@keyframes rolling {
  0% {
    transform: translateX(0%);
  }
  /* 50%{
      transform: translateX(-100%);
   }
   50.01%{
      transform: translateX(100%);
   } */
  100% {
    transform: translateX(-12%);
  }
}

@keyframes onOff {
  0% {
    opacity: 0;
    transform: translateY(20%);
  }
  50% {
    opacity: 0.5;
    transform: translateY(60%);
  }
  100% {
    opacity: 1;
    transform: translateY(100%);
  }
}
